.post-view-wrapper {
    width: 60%;
    margin: 0 auto;
}

.post-view-go-list-btn {
    border : 0;
    padding: 10px;
    background-color: #ffd9d9;
}

.post-view-go-list-btn:hover {
    cursor: pointer;
}

.post-view-row {
    margin : 10px 0;
    display: flex;
}

.post-view-row > label:first-child {
    margin : 10px 0;
    width: 30%;
    font-weight : bold;
}

.post-view-row > *:nth-child(2) {
    margin: 10px 0;
    width: 70%;
}